import React from 'react';
import {NavLink} from "react-router-dom"
import Cookie from 'js-cookie'
import Pubsub from 'pubsub-js'
class Heads extends React.Component{
    constructor(){
        super()
        this.state={
            auth:""
        }
    }
    componentDidMount(){
        let self=this
        Pubsub.subscribe("auth",function(msg,data){
            self.setState({
                auth:data
            })
        })
    }
    render(){
        if(Cookie.get("auth")){
            return (
                <div>
                    <nav className="navbar navbar-light">
                    <div className="container">
                        <a className="navbar-brand" href="index.html">conduit</a>
                        <ul className="nav navbar-nav pull-xs-right">
                        <li class="nav-item">
                            <NavLink class="nav-link active" to='/' >Home</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/editor" activeClassName='active'>
                            <i className="ion-compose"></i>&nbsp;New Post
                            </NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/setting" activeClassName='active'>
                            <i className="ion-gear-a"></i>&nbsp;Settings
                            </NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link ng-binding active" ui-sref-active="active" ui-sref="app.profile.main({ username: $ctrl.currentUser.username })"  to="/profile" activeClassName='active'>
                                <img ng-src='https://z3.ax1x.com/2021/07/03/RRUVbD.jpg' className='user-pic' src="https://z3.ax1x.com/2021/07/03/RRUVbD.jpg"/>ypa
                            </NavLink>
                        </li>
                        </ul>
                    </div>
                    </nav>
                </div>
                )
        }else{
            return (
                <div>
                    <nav className="navbar navbar-light">
                    <div className="container">
                        <a className="navbar-brand" href="index.html">conduit</a>
                        <ul className="nav navbar-nav pull-xs-right">
                        <li class="nav-item">
                            <NavLink class="nav-link active" to='/' >Home</NavLink>
                        </li>
                        
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/login" activeClassName='active'>Sign in</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/register" activeClassName='active'>Sign up</NavLink>
                        </li>
                        
                        </ul>
                    </div>
                    </nav>
                </div>
                )
        }
       
    }
}
export default Heads