import React from 'react'
import request from '../request/api'
import Cookie from 'js-cookie'
import Pubsub from 'pubsub-js'
class A extends React.Component{
    constructor(props){
        super(props)
        this.state={}
        this.blur=this.blur.bind(this)
        
    }
    blur(ev){
        var value=ev.target.value
        Pubsub.publish("value",value)
    }
    render(){
        return (
            <fieldset className="form-group">
            <input className="form-control form-control-lg" type="text" placeholder="Your Name" onBlur={this.blur}/>
            </fieldset>
        )
    }
}
class Login extends React.Component {
    constructor(props){
        super(props)
        this.state={
            username:"",
            email:"11111",
            password:"11111"
        }
        this.onsubmit=this.onsubmit.bind(this)
    }
    componentDidMount(){
       var self=this
       Pubsub.subscribe("value",function(mes,data){
           self.setState({
               username:data
           })
       })
    }
    onsubmit(event){
        
        
            var user={
                username:this.state.username?this.state.name:"",
                email:this.state.email,
                password:this.state.password
            }
    
        
        console.log(this.state.username)
        console.log(user)
        request({
            method:'POST',
            url:this.state.username?"/api/users":'/api/users/login',
            data:{
                user:user
            }

        }).then(res=>{
            Cookie.set("auth",res.data.user.token)
            this.props.history.push("/")
            Pubsub.publish('auth',res.data.user.token)
            console.log(res)
        })
        event.preventDefault()
    }
    onblur1(ev){
        this.setState({
            email:ev.target.value
        })
        console.log(ev.target)
        console.log(this)
    }
    onblur2(ev){
        this.setState({
            password:ev.target.value
        })
    }
    islogin(){
        return this.props.match.path==='/login'
       }
 render(){
   return (
    <div className="auth-page">
        <div className="container page">
        <div className="row">
    
            <div className="col-md-6 offset-md-3 col-xs-12">
            <h1 className="text-xs-center">
                {this.islogin()?"Sing in":"Sing up" }
                </h1>
            <p className="text-xs-center">
                <a href="">
                {this.islogin()?"Need an account?":"Have an account?" }
                </a>
            </p>
    
            <ul className="error-messages">
                <li>That email is already taken</li>
            </ul>
    
            <form onSubmit={this.onsubmit.bind(this)}>
                {this.islogin()?"":<A/>}
                <fieldset className="form-group">
                <input className="form-control form-control-lg" type="text" placeholder="Email" onBlur={this.onblur1.bind(this)}/>
                </fieldset>
                <fieldset className="form-group">
                <input className="form-control form-control-lg" type="password" placeholder="Password" onBlur={this.onblur2.bind(this)} />
                </fieldset>
                <button className="btn btn-lg btn-primary pull-xs-right">
                {this.islogin()?"Sing in":"Sing up" }
                </button>
            </form>
            </div>
    
        </div>
        </div>
    </div>
  
   )
 }
}

export default Login