import React from 'react'
import request from '../request/api'
import {addFavorite,deleteFavorite} from '../request/favorite'

class Home extends React.Component {
    constructor(props){
        super(props)
        this.state={
            data:{
                articles:[]
            },
            activePage:1,
            pages:[],
            favoritedisable:false
        }
        this.fnFavorite=this.fnFavorite.bind(this)
    }
    componentDidMount(){
        
        request({
            method:"GET",
            url:"/api/articles",
            params:{

            }
        }).then(res=>{
            console.log(res)
            var pages=[]
            for(var i=0;i<res.data.articlesCount/20;i++){
                pages.push(i)
            }
            this.setState({
                data:res.data,
                pages
            })
        })
    }
    async fnFavorite(data){
        if(data.favorite){
            await deleteFavorite(data).then(res=>{
                console.log(res)
            })
            data.favorite=false
            data.favoritesCount-=1
            let key=this.state.data.articles.indexOf(data)
            this.state.data.articles[key]=data
             this.setState({
                data:{
                    articles:this.state.data.articles
                },
                favoritedisable:false
            })
        }else{
            await addFavorite(data).then(res=>{
            console.log(res)
            data.favorite=true
            data.favoritesCount+=1
            let key=this.state.data.articles.indexOf(data)
            this.state.data.articles[key]=data
            
             this.setState({
                data:{
                    articles:this.state.data.articles
                },
                favoritedisable:true
            })
           })
        }
       
        
    }
 render(){
   return (
    <div className="home-page">

            <div className="banner">
            <div className="container">
                <h1 className="logo-font">conduit</h1>
                <p>A place to share your knowledge.</p>
            </div>
            </div>
        
            <div className="container page">
            <div className="row">
        
                <div className="col-md-9">
                <div className="feed-toggle">
                    <ul className="nav nav-pills outline-active">
                    <li className="nav-item">
                        <a className="nav-link disabled" href="">Your Feed</a>
                    </li>
                    <li className="nav-item">
                        <a className="nav-link active" href="">Global Feed</a>
                    </li>
                    </ul>
                </div>
        
                {
                    this.state.data.articles.map((item,index)=>{
                        return (
                            <div className="article-preview" key={index}>
                                <div className="article-meta">
                                <a href="profile.html"><img src={item.author.image} /></a>
                                <div className="info">
                                    <a href="" className="author">{item.author.name}</a>
                                    <span className="date">{item.createdAt}</span>
                                </div>
                                <button className="btn btn-outline-primary btn-sm pull-xs-right" onClick={()=>{this.fnFavorite(item)}} disabled={this.state.favoritedisable}>
                                    <i className="ion-heart"></i> {item.favoritesCount}
                                </button>
                                </div>
                                <a href="" className="preview-link">
                                <h1>{item.title}</h1>
                                <p>{item.description}</p>
                                <span>Read more...</span>
                                </a>
                            </div>
                        )
                    })
                }
                <ul className="pagination">
                   {
                       this.state.pages.map((item,index)=>{
                           return (
                            <li className={this.activePage==1? "active page-item  ":"page-item "} key={index}>
                            <a className="page-link " href="">{index+1}</a>
                            </li>
        
                           )
                       })
                   }
                </ul>
                
        
                
        
                </div>
                
        
                <div className="col-md-3">
                <div className="sidebar">
                    <p>Popular Tags</p>
        
                    <div className="tag-list">
                    <a href="" className="tag-pill tag-default">programming</a>
                    <a href="" className="tag-pill tag-default">javascript</a>
                    <a href="" className="tag-pill tag-default">emberjs</a>
                    <a href="" className="tag-pill tag-default">angularjs</a>
                    <a href="" className="tag-pill tag-default">react</a>
                    <a href="" className="tag-pill tag-default">mean</a>
                    <a href="" className="tag-pill tag-default">node</a>
                    <a href="" className="tag-pill tag-default">rails</a>
                    </div>
                </div>
                </div>
        
            </div>
            </div>
        
        </div>
  
   )
     
   
 }
}

export default Home