
import './main.css';
import React from 'react'
import Heads from './Heads'
import Foots from './Foots'
import Home from './Home'
import Article from './Article'
import Login from './Login'
import Setting from './Setting'
import {BrowserRouter as Router, Route,Switch} from 'react-router-dom'
import Profile from './Profile'
import Editor from './Editor'
import Cookie from 'js-cookie'
class App extends React.Component {
  constructor(){
    super()
    this.state={}
  }
  render(){
    if(Cookie.get("auth")){
      return (
        <div>
          <Router>
          <Heads></Heads>
            <Switch>
              <Route exact path='/'>
              <Home></Home>
              </Route>
              <Route exact path='/editor'>
              <Editor></Editor>
              </Route>
              <Route exact path='/login' component={Home}></Route>
              <Route exact path='/register' component={Home}></Route>
              <Route exact path='/setting' component={Setting}></Route>
              <Route exact path='/article' component={Article}></Route>
              <Route exact path='/profile' component={Profile}></Route>
            </Switch>
            <Foots></Foots>
          </Router>
          
          
        </div>
      )
    }else{
      return (
        <div>
          <Router>
          <Heads></Heads>
            <Switch>
              <Route exact path='/'>
              <Home></Home>
              </Route>
              <Route exact path='/editor'>
              <Editor></Editor>
              </Route>
              <Route exact path='/login' component={Login}></Route>
              <Route exact path='/register' component={Login}></Route>
              <Route exact path='/setting' component={Login}></Route>
              <Route exact path='/article' component={Login}></Route>
              <Route exact path='/profile' component={Login}></Route>
            </Switch>
            <Foots></Foots>
          </Router>
          
          
        </div>
      )
    }
   
  }
  }
  

export default App;
